/* Hover effect for table rows */
tr:hover {
  background-color: #1f2937; /* Adjust color as needed */
}

/* Custom scrollbar for overflow */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
