@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.dashboard-container {
  animation: fadeIn 1s ease-out;
}

.content-wrapper:hover {
  background-color: #008b8b; /* Adjust the color for hover effect */
}

.title,
.welcome-message {
  transition: color 0.3s ease-in-out;
}

.title:hover,
.welcome-message:hover {
  color: #e2e8f0; /* Lighter color on hover */
}
