/* Animation for logo */
.logo-container img {
  transition: transform 0.3s ease-in-out;
}

.logo-container img:hover {
  transform: scale(1.1);
}

/* Animation for Greeting */
.greeting p {
  transition: transform 0.3s ease-in-out, color 0.3s ease-in-out;
}

.greeting p:hover {
  color: #e2e8f0; /* Lighter color on hover */
  transform: translateY(-5px);
}

/* Animation for Navigation Links */
.nav-link {
  transition: color 0.3s ease-in-out;
}

.nav-link:hover,
.nav-link.active {
  color: #e2e8f0; /* Lighter color on hover */
}

/* Animation for Logout Button */
.logout-button {
  transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.logout-button:hover {
  background-color: #666666; /* Darker shade on hover */
  transform: translateY(-3px);
}

/* General Styling for Header */
.header-container {
  transition: background-color 0.3s ease-in-out;
}

.header-container:hover {
  background-color: #008b8b; /* Slightly different shade on hover */
}
