/* Reuse the fade-in animation from ChargeMasterPage.css */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.eligibility-page {
  animation: fadeIn 1s ease-out;
}

/* Animation for state transitions */
.state-transition {
  animation: fadeInScale 0.5s ease-out;
}

