/* Fade-in animation for the entire page */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Animation for state transitions */
@keyframes fadeInScale {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.charge-master-page {
  animation: fadeIn 1s ease-out;
}

/* Styling for the main container */
.main-container {
  background-color: #00e3d2; /* Light teal background */
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Header styling */
.header {
  color: #ffffff;
  margin-bottom: 1rem;
  font-size: 2rem;
  font-weight: bold;
}

/* General styling for list and table components */
.list-container,
.table-container {
  background-color: #999999;
  border: 1px solid #707070; /* Adjusted for correct color code */
  border-radius: 0.25rem;
  margin-top: 1rem;
  padding: 1rem;
  transition: box-shadow 0.3s ease-in-out;
  animation: fadeInScale 0.5s ease-out;
}

.list-container:hover,
.table-container:hover {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

/* Button and interactive element styling */
.button {
  background-color: #007d75; /* Dark teal */
  color: white;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.button:hover {
  background-color: #005f57; /* Slightly darker teal */
}
