/* Hover effect for table row */
tr:hover {
  background-color: #2a2a2a; /* A darker shade for hover */
  transition: background-color 0.3s ease;
}

/* Animation for Add button */
.add-button {
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.add-button:hover {
  background-color: #008b8b; /* Slightly different color on hover */
  transform: translateY(-2px);
}

/* Fade-in effect for new rows */
@keyframes fadeInRow {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

tr {
  animation: fadeInRow 0.5s ease-out;
}
